import { Component, AfterViewInit, Output, EventEmitter } from '@angular/core';
import { NAVBAR } from  './navbar-items';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from 'src/app/services/language.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements AfterViewInit  {
  public navbarItems: any[];
  public flagChange;

  constructor(
    private translate: TranslateService,
    private languageService: LanguageService) {
      const browserLang = this.translate.getBrowserLang();
      this.useLanguage(browserLang);
   }

  ngOnInit() {
    this.navbarItems = NAVBAR;
  }

  useLanguage(language: string) {
    this.translate.use(language);
    this.languageService.languageEmitter.emit(language);
    switch (language) {
      case 'en':
        return this.flagChange = 'flag-icon-us';
        break;
      case 'pt':
        return this.flagChange = 'flag-icon-br';
        break;
    }
  }

  ngAfterViewInit() {
    $(window).on('scroll', function(){
      if( $(window).scrollTop()>80 ){
        $('#sticky').addClass('stick');
      } else {
        $('#sticky').removeClass('stick');
      }
    }); 
  }

}
