import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { SendService } from 'src/app/services/send.service';
import swal from 'sweetalert2';

declare var require: any;
const data: any = require('./data.json');

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  public contact: any; 
  public submittingForm: boolean = false;
  public translateMsg;
  sendEmailForm = new FormGroup({
    name: new FormControl(),
    company: new FormControl(),
    email: new FormControl(),
    phonenumber: new FormControl(),
    message: new FormControl()
  });

  constructor(
    private fb: FormBuilder,
    private translate: TranslateService,
    private sendService: SendService
    ) { }

  ngOnInit() {
    this.contact = data;
    this.translateMessage();
    this.createForm();
  }

  errorHandling = (control: string, error: string) => {
    return this.sendEmailForm.controls[control].hasError(error);
  }

  onSubmit() {
    this.submittingForm = true;
    this.sendService.sendMail(this.sendEmailForm.value).subscribe(data => {
      swal({
        title: this.translateMsg.success,
        html: this.translateMsg.sendingContactSuccessfully,
        type: 'info'});
      this.submittingForm = false;
      this.createForm();
    }, error => {
      swal({
        title: this.translateMsg.error,
        html: this.translateMsg.errorSendingContact,
        type: 'error'});
      this.submittingForm = false;
    });
  }

  private createForm() {
    this.sendEmailForm = this.fb.group({
      name: ['', Validators.required],
      company: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      phonenumber: ['', Validators.required],
      message: ['', Validators.required]
    });
  }

  private translateMessage() {
    this.translate.get('message').subscribe(res => {
      this.translateMsg = res;
    });
  }

}
