import {Component, OnInit} from '@angular/core';

@Component({
    selector: 'app-footer',
    template: 
   `<div class="footer-area">
        <div class="container">                
            <div class="row">
                <div class="col-sm-12 text-center">
                    <div class="copyright">
                        <p>{{ copyright }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>`,
})
export class FooterComponent implements OnInit{
    public copyright: string = '© 2020 Ailson Costa. All rights reserved.';
    
    constructor() {};

    ngOnInit() {}
}
